<script>
  import { onMount } from 'svelte';
  import { users } from './store.js';
  
  let fields = ["id", "id_str", "name", "screen_name", "location", "description", "url", "entities", "protected", "followers_count", "friends_count", "listed_count", "created_at", "favourites_count", "utc_offset", "time_zone", "geo_enabled", "verified", "statuses_count", "lang", "contributors_enabled", "is_translator", "is_translation_enabled", "profile_background_color", "profile_background_image_url", "profile_background_image_url_https", "profile_background_tile", "profile_image_url", "profile_image_url_https", "profile_banner_url", "profile_link_color", "profile_sidebar_border_color", "profile_sidebar_fill_color", "profile_text_color", "profile_use_background_image", "has_extended_profile", "default_profile", "default_profile_image", "following", "follow_request_sent", "notifications", "translator_type"];
  
  let keyFields = [ 
    { n:'profile_image_url', w: 10 }, 
    { n: 'screen_name', w: 20 }, 
    { n: 'name', w: 19 },
    { n: 'location', w: 15 },
    { n: 'description', w: 30 },
    {n: 'url', w: 15 }
  ];
  
  
  onMount(() => {	
    let urly = './users_1.json';

    fetch(urly)
        .then(res => res.json())
        .then(data => {
            $users = data;
        let fields = Object.keys(data[0]);
        //console.log(fields);
    });     
  })
  
</script>

<style>

  :global(body) {
    margin: 0;
    font-size: 25px;
    overflow-x: hidden;
  }
  .profileImg {
    width: 75px;
    height: 75px;
  }
  #wrapper {
    display:flex;
    flex-flow: row wrap;
  }
  #left {
    width: 20%;
  }
  main {
    width: 80%;
    max-height: 93vh;
    overflow-x: auto;
  }
  table {
    table-layout: fixed;
    width: 100%;
  }
  td {
    overflow: hidden;
  }
  header {
    background-color: #2d3e50; 
    font-size: .9rem;
    color: white;
    min-height: 60px;
    display:flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 4px;
  }
  footer {
    background-color: lightblue;
    height: 50px;
  }
  section,
  aside {
    background-color: #eee;
  }
</style>

<header >
    <div>Twitter Flock Dashboard</div>
</header>

<div id="wrapper">

    <section id="left"></section>
    <main>
    {#if $users && $users.length > 0}
        <table class="pure-table">
        <thead>
            <tr>
                {#each keyFields as field}
                    <th style="width: {field.w}%">{field.n.replace('profile_image_url', 'image').replace('screen_name', 'handle')}</th>
                {/each}
            </tr>
        </thead>
            {#each $users as user, y}
                <tr>
                    {#each keyFields as field}
                        {#if field.n == 'profile_image_url'}
                              <td width="100">
                                <img class="profileImg" src={user[field.n]}/>
                              </td>
                        {:else}
                            {#if field.n == 'url'}
                                <td>
                                    {#if user[field.n]}
                                        <a href="user[{field.n}]" target="_blank" rel="noopener noreferrer">âœ”</a>
                                    {/if}
                                    
                                </td>
                                
                            {:else}
                                <td>{(field.n == 'screen_name' ? '@'+ user[field.n] : user[field.n]) }</td>
                            {/if}
                        {/if}
                    {/each}
                </tr>
            {/each}
        </table>
    {/if}
    </main>
</div>
